<template>
	<el-row class="container">
		<el-col :span="24" class="header">
			<el-col :span="10" class="logo" :class="collapsed?'logo-collapse-width':'logo-width'">
				<!--{{collapsed?'':sysName}}-->
				<img src="../assets/logotitle.png" style="width: 100px;height: 40px;float: right;"/>
				<!--<img src="../assets/logotitle.png" style="width: 70px;height: 70px;float: right;margin-top: -5px;"/>-->
			</el-col>
			<el-col :span="10" class="logo-width">
				{{logoTitle}}
			</el-col>
			<el-col :span="4" class="userinfo">
				<el-dropdown trigger="hover" style="float: right;">
					<span class="el-dropdown-link userinfo-inner">欢迎您:{{user.username}}
					<i class="el-icon-caret-bottom el-icon--right"></i>
					</span>
					<el-dropdown-menu slot="dropdown">
						<el-dropdown-item @click.native="goMyCenter">个人中心</el-dropdown-item>
						<el-dropdown-item @click.native="changePwdModelVisible=true">修改密码</el-dropdown-item>
						<el-dropdown-item divided @click.native="logout">退出登录</el-dropdown-item>
					</el-dropdown-menu>
				</el-dropdown>
			</el-col>
		</el-col>
		<el-col :span="24" class="main">
			<aside :class="collapsed?'menu-collapsed':'menu-expanded'">
				<!--导航菜单-->
				<el-menu theme="dark" :default-active="$route.path" class="el-menu-vertical-demo" @open="handleopen" @close="handleclose" @select="handleselect" unique-opened router v-show="!collapsed">
					<template v-for="(item,index) in $router.options.routes" v-if="!item.hidden&&(item.name!='物业管理'||sysFlag)&&(item.name!='用户管理'||user.username=='admin')"><!--!item.hidden&&(item.name!='物业管理'||item.name!='用户管理'||sysFlag)-->
						<el-submenu :index="index+''" v-if="!item.leaf">
							<template slot="title"><i :class="item.iconCls" style="font-size:18px"></i>{{item.name}}</template>
							<el-menu-item v-for="child in item.children" :index="child.path" :key="child.path" v-if="!child.hidden&&(child.name!='账单录入'||sysFlag)">{{child.name}}</el-menu-item>
						</el-submenu>
						<el-menu-item v-if="item.leaf&&item.children.length>0" :index="item.children[0].path"><i :class="item.iconCls"></i>{{item.children[0].name}}</el-menu-item>
					</template>
				</el-menu>
			</aside>
			<section class="content-container">
				<div class="grid-content bg-purple-light">
					<el-col :span="24" class="breadcrumb-container">
						<strong class="title">{{$route.name}}</strong>
						<el-breadcrumb separator="/" class="breadcrumb-inner">
							<el-breadcrumb-item v-for="item in $route.matched" :key="item.path">
								{{ item.name }}
							</el-breadcrumb-item>
						</el-breadcrumb>
					</el-col>
					<el-col :span="24" class="content-wrapper">
						<transition name="fade" mode="out-in">
							<router-view></router-view>
						</transition>
					</el-col>
				</div>
				<el-dialog title="修改密码" v-model="changePwdModelVisible" :close-on-click-modal="false">
					<el-form label-width="100px">
						<el-form-item label="旧密码" :rule="{required: true}">
							<el-input v-model="oldPassword"></el-input>
						</el-form-item>
						<el-form-item label="新密码" :rule="{required: true}">
							<el-input v-model="newPassword"></el-input>
						</el-form-item>
						<el-form-item label="确认密码" :rule="{required: true}">
							<el-input v-model="confirmPassword"></el-input>
						</el-form-item>
					</el-form>
					<div slot="footer" class="dialog-footer">
						<el-button @click="changePwdModelVisible = false">取消</el-button>
						<el-button type="primary" @click="changePwd" :loading="Loading">提交</el-button>
					</div>
				</el-dialog>
			</section>
		</el-col>
	</el-row>
</template>

<script>import {
	baseUtile
} from '../api/baseUtile';

export default {
	data() {
		return {
			sysName: '掌屏科技',
			collapsed: false,
			user: {},
			form: {
				name: '',
				region: '',
				date1: '',
				date2: '',
				delivery: false,
				type: [],
				resource: '',
				desc: ''
			},
			changePwdModelVisible: false,
			Loading: false,
			logoTitle: "",
			oldPassword: "",
			newPassword: "",
			confirmPassword: "",
			sysFlag:false
		}
	},
	methods: {
		onSubmit() {
			console.log('submit!');
		},
		handleopen() {
			//console.log('handleopen');
		},
		handleclose() {
			//console.log('handleclose');
		},
		handleselect: function(a, b) {},
		//退出登录
		logout: function() {
			var _this = this;
			this.$confirm('确认退出吗?', '提示', {
				//type: 'warning'
			}).then(() => {
				sessionStorage.removeItem('user');
				_this.$router.push('/login');
			}).catch(() => {

			});

		},
		//折叠导航栏
		collapse: function() {
			this.collapsed = !this.collapsed;
		},
		showMenu(i, status) {
			this.$refs.menuCollapsed.getElementsByClassName('submenu-hook-' + i)[0].style.display = status ? 'block' : 'none';
		},
		changePwd() {
			if(!this.oldPassword) {
				baseUtile.tip("请输入旧密码!");
				return;
			}
			if(!this.oldPassword) {
				baseUtile.tip("请输入新密码!");
				return;
			}
			if(!this.oldPassword) {
				baseUtile.tip("请输入确认密码!");
				return;
			}
			var params = {
				oldPassword: this.oldPassword,
				newPassword: this.newPassword,
				confirmPassword: this.confirmPassword,
			}
			var that = this;
			this.Loading = true;
			baseUtile.httpSend("/personal_info.html", params, data => {
				if(data.r == true || data.r == "true") {
					baseUtile.tip(data.m, "success");
					that.changePwdModelVisible = false;
				} else {
					baseUtile.tip(data.m)
				}
				that.Loading = true;
			})
		},
		goMyCenter: function() {
			this.$router.push({
				path: '/info'
			});
		}
	},
	mounted() {
		this.user = JSON.parse(sessionStorage.getItem('user'));
		this.logoTitle = document.title;
		if(!this.user.isSaler) {
			this.sysFlag = true;
		}
	}
}</script>

<style scoped lang="scss">@import '~scss_vars';
.el-pagination {
	padding-right: 0px;
}

.container {
	position: absolute;
	top: 0px;
	bottom: 0px;
	width: 100%;
	.header {
		height: 60px;
		line-height: 60px;
		background: $color-primary;
		color: #fff;
		.userinfo {
			text-align: right;
			padding-right: 35px;
			float: right;
			.userinfo-inner {
				cursor: pointer;
				color: #fff;
				img {
					width: 40px;
					height: 40px;
					border-radius: 20px;
					margin: 10px 0px 10px 10px;
					float: right;
				}
			}
		}
		.logo {
			//width:230px;
			height: 60px;
			font-size: 22px;
			padding-left: 20px;
			padding-right: 20px;
			border-color: rgba(238, 241, 146, 0.3);
			/*border-right-width: 1px;
				border-right-style: solid;*/
			img {
				width: 140px;
				width: 50px;
				float: left;
				margin: 10px 10px 10px 18px;
			}
			.txt {
				color: #fff;
			}
		}
		.logo-width {
			width: 230px;
		}
		.logo-collapse-width {
			width: 60px
		}
		.tools {
			padding: 0px 23px;
			width: 14px;
			height: 60px;
			line-height: 60px;
			cursor: pointer;
		}
	}
	.main {
		display: flex;
		background: #f2f2f2;
		position: absolute;
		top: 60px;
		bottom: 0px;
		overflow: hidden;
		margin-top: 30px;
		padding-left: 40px;
		padding-right: 40px;
		aside {
			flex: 0 0 200px;
			width: 150px;
			// position: absolute;
			// top: 0px;
			// bottom: 0px;
			.el-menu {
				height: 100%;
				overflow-y: scroll;
				overflow-x: hidden;
			}
			.collapsed {
				width: 60px;
				.item {
					position: relative;
				}
				.submenu {
					position: absolute;
					top: 0px;
					left: 60px;
					z-index: 99999;
					height: auto;
					display: none;
				}
			}
		}
		.menu-collapsed {
			flex: 0 0 60px;
			width: 60px;
		}
		.menu-expanded {
			flex: 0 0 200px;
			width: 200px;
			overflow-x: hidden;
		}
		.content-container {
			background: #FFFFFF;
			flex: 1;
			// position: absolute;
			// right: 0px;
			// top: 0px;
			// bottom: 0px;
			// left: 230px;
			overflow-y: scroll;
			padding: 20px;
			.breadcrumb-container {
				//margin-bottom: 15px;
				.title {
					width: 200px;
					float: left;
					color: #475669;
				}
				.breadcrumb-inner {
					float: right;
				}
			}
			.content-wrapper {
				background-color: #fff;
				box-sizing: border-box;
			}
		}
	}
}</style>


